.our-picture {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  height: 100vh;

  img {
    height: 100vh;
    width: auto;
  }
}

.intro-image {
  position: relative;

  .text {
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 35px;
    font-weight: bold;
  }
}