.job-description {
  .header {
    display: flex;
    flex-wrap: wrap;

    .title {
      width: 50%;
      text-align: left;
      font-size: 16px;
      font-weight: bold;
      padding-bottom: 3px;
    }

    .sub-title {
      width: 50%;
      font-size: 14px;
      font-weight: bold;
      padding-bottom: 5px;
    }

    .employer {
      text-align: left;
    }

    .date-range {
      text-align: right;
    }
  }
}
